*{
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
body{
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  background: #f0f0f0;

}
h2{
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: white;
  padding: 30px 0;
}
#hey{
  border:2px solid white;
  margin:20px;
  padding:20px;
  display:flex;
  flex-direction:column;
    justify-content:center;
}
.head{
  background-color:#7D6E83;
  color: white;
  border: 2px solid white;
  text-align: center;
  height: 50px;
}

/* Table Styles */

.table-wrapper{
  
  display: flex;
  flex-direction: column;
  

  box-shadow: 0px 35px 50px rgba( 0, 0, 0, 0.2 );
}
.fl-table {
  border-radius: 5px;
  font-size: 12px;
  font-weight: normal;
  border: none;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  white-space: nowrap;
  background-color: white;
}

.fl-table td, .fl-table th {
  text-align: center;
  padding: 8px;
}

.fl-table td {
  border-right: 1px solid #f8f8f8;
  font-size: 12px;
}

.fl-table thead th {
  color: #ffffff;
  background: #4FC3A1;
}


.fl-table thead th:nth-child(odd) {
  color: #ffffff;
  background: #324960;
}

.fl-table tr:nth-child(even) {
  background: #F8F8F8;
}
.header{
  text-align: center;
  font-size: 20px;
  background-color: #DFD3C3;
  border: 2px solid white;
}
/* Responsive */

@media (max-width: 767px) {
  .fl-table {
      display: block;
      width: 100%;
  }
  .header{
    text-align: center;
    font-size: 14px;
    margin-top: 10px;
    background-color: #DFD3C3;
    border: 2px solid white;
  }
  .head{
    background-color:#7D6E83;
    color: white;
    border: 2px solid white;
    text-align: center;
    height: 50px;
    font-size: 10px;
    width: 50px !important;
  }
  .table-wrapper:before{
    
      display: block;
      text-align: right;
      font-size: 11px;
      color: white;
      padding: 0 0 10px;
  }
  .fl-table thead, .fl-table tbody, .fl-table thead th {
      display: block;
  }
  .fl-table thead th:last-child{
      border-bottom: none;
  }
  .fl-table thead {
      float: left;
  }
  .fl-table tbody {
      width: auto;
      position: relative;
      overflow-x: auto;
  }
  .fl-table td, .fl-table th {
      padding: 20px .625em .625em .625em;
      height: 60px;
      vertical-align: middle;
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      width: 120px;
      font-size: 13px;
      text-overflow: ellipsis;
  }
  .fl-table thead th {
      text-align: left;
      border-bottom: 1px solid #f7f7f9;
  }
  .fl-table tbody tr {
      display: table-cell;
  }
  .fl-table tbody tr:nth-child(odd) {
      background: none;
  }
  .fl-table tr:nth-child(even) {
      background: transparent;
  }
  .fl-table tr td:nth-child(odd) {
      background: #F8F8F8;
      border-right: 1px solid #E6E4E4;
  }
  .fl-table tr td:nth-child(even) {
      border-right: 1px solid #E6E4E4;
  }
  .fl-table tbody td {
      display: block;
      text-align: center;
  }
  .btn-dark{
    background-color: #7D6E83 !important;
    border: 2px solid white;
    margin-top: 5px;
    font-size: 10px;
  }
  #hello{
    font-size: 10px;
  }
  #hey{
    padding: 10px;
  }
}


.backlog{
  background-color: #F8EDE3;
  border: 2px solid white;
  
}
.progress{
  background-color: #DFD3C3;
}
.finished{
  background-color: #D0B8A8;
}

.btn-dark{
  background-color: #7D6E83 !important;
  border: 2px solid white;
  margin-top: 5px;
}